import { ADD_PERSON_TO_FAVORITE, REMOVE_PERSON_ROM_FAVORITE } from '../constants/actionTypes';


export const setPersonToFavorite = person => ({
  type: ADD_PERSON_TO_FAVORITE,
  payload: person,
});

export const removePersonFromFavorite = personId => ({
  type: REMOVE_PERSON_ROM_FAVORITE,
  payload: personId,
});